* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  background-color: #d2dbdd;
}

.heading {
  padding: 30px 0;
  text-align: center;
  font-family: "Carter One", cursive;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #fff;
  font-size: 1.5rem;
  margin-bottom: 30px;
  text-shadow: 0 1px 0 #868686, 0 2px 0 #868686, 0 3px 0 #868686,
    0 4px 0 #868686, 0 30px 5px rgba(0, 0, 0, 1);
}

.card_img {
  max-width: 100%;
  height: 400px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}
.container {
  display: inline-flex;
  justify-content: center;
  min-width: 21%;
  margin: 2%;
  align-items: center;
}
.card {
  transition: all 1s cubic-bezier(0.175, 0.885, 0, 1);
  border-radius: 12%;
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
}

.card:hover {
  box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
  transform: scale(1.05, 1.05);
}

.card_info {
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  padding: 16px 24px 24px 24px;
}

.card_category {
  font-family: sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 600;
  margin-left: 30px;
}

.card_title {
  margin-top: 5px;
  margin-bottom: 10px;
  font-family: sans-serif;
  text-align: center;
}

button {
  padding: 5px 8px;
  font-family: sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #fff;
  outline: none;
  border: 1px solid black;
  cursor: pointer;
  border-radius: 20px;
  margin-bottom: 20px;
}
button:hover {
  background-color: tomato;
  color: #fff;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
}

a {
  text-decoration: none;
}
.align {
  text-align: center;
}
@media only screen and (max-width: 1450px) {
  .container {
    min-width: 29.3%;
    margin: 2%;
  }
}

@media only screen and (max-width: 1039px) {
  .container {
    margin: 9%;
  }
}

@media only screen and (max-width: 956px) {
  .container {
    display: inline-flex;
    min-width: 2%;
    margin: 8%;
  }
  .card_img {
    height: 400px;
  }
}

@media only screen and (max-width: 802px) {
  .container {
    display: inline-flex;
    min-width: 2%;
    margin: 7%;
  }
}

@media only screen and (max-width: 758px) {
  .container {
    display: inline-flex;
    min-width: 2%;
    margin: 6%;
  }
}

@media only screen and (max-width: 719px) {
  .container {
    display: inline-flex;
    min-width: 2%;
    margin: 5%;
  }
}

@media only screen and (max-width: 684px) {
  .container {
    display: flex;
    /* max-width: 100%; */
    margin: 9%;
  }
  .card {
    /* max-width: 100%; */
  }
  .card_img {
    width: 450px;
    height: 600px;
  }
}

@media only screen and (max-width: 500px) {
  .card {
    /* max-width: 100%; */
  }
  .card_img {
    width: 300px;
    height: 400px;
  }
}
